

import React from 'react';


 class LoginButtton extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      loginDialogOpen :false
    };

    this.handleLoginDialogOpen = this.handleLoginDialogOpen.bind(this);
    this.handleLoginDialogClose = this.handleLoginDialogClose.bind(this);
  }

  handleLoginDialogOpen(){ this.setState({loginDialogOpen: true}); }

  handleLoginDialogClose(){ this.setState({loginDialogOpen: false}); }

  

  render() {

 const actions = [
      <FlatButton
        label="Cancel"
        primary={true}
        onTouchTap={this.handleClose}
      />,
      <FlatButton
        label="Submit"
        primary={true}
        keyboardFocused={true}
        onTouchTap={this.handleClose}
      />,
    ];



    
    return (
     <div> <FlatButton label="Login" onTouchTap={this.handleLoginDialogOpen}/> 
        <Dialog
          title="Dialog With Actions"
          actions={actions}
          modal={false}
          open={this.state.open}
          onRequestClose={this.handleClose}
        >
          The actions in this window were passed in as an array of React objects.
        </Dialog>

 </div>
    );
  }
}

export default LoginButtton;


